*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: #a8cdd7;;
}


li{

  list-style: none;
  border-radius: 3px;
}

li+li{
  margin-top: 10px;
}

h1{
  text-align: center;
  margin-bottom: 50px;
}

.container{
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.pagination-list{
  margin-bottom: 20px;
}

.pagination-btn{
  display: inline-block;
  
}

.pagination-btn + .pagination-btn{
  margin-left: 25px;
}

.list{
  background-color: azure;
  padding: 15px;
}

.list--active{
  border: 1.5px solid #55a7bd;
}

.btn{
  padding: 10px;
}

.btn--active{
  background-color: #b6b5d9;
}

.btn + .btn{
  margin-left: 20px;
}

.checkbox{
  margin-right: 15px;
}